import React, { useState } from "react";

function ShareButton(props) {
  const [open, setOpen] = useState(false);

  return (
    <li className="nav-item">
      <a id="left" href="#" className="share" onClick={() => setOpen(!open)}>
        Share
      </a>
      {open && props.children}
    </li>
  );
}

export default ShareButton;
